.logo{
  display: block;
  width: 100%;
  height: 64px;
  background-color: #eee;
}



.infomation{
  display: flex;
  justify-content: space-between;
}

.device_info{
  display: flex;
  flex-direction: column;
}

.device_model.tit{
  margin: 16px 0 0;
  font-size: 18px;
  font-weight: 700;
}

.user__box{
  display: flex;
  align-items: center;
  font-size: 14px;
  color: #999;
  margin: 16px 0 0;
}

.user__box span:first-child{ margin-right: 8px}


.menu{
  width: 100%;
  height: auto;
  margin-top: 40px;
  padding: 0 0 0 20px;
  font-size: 22px;
  box-sizing: border-box;
}


.menu > li {
  height: auto;
  margin: 0 0 36px;
  cursor: pointer;
  overflow: hidden;
}

.menu > li > .menu__btn{
  height: 36px;
  width: 100%;
  display: flex;
  align-items: center;
  user-select: none;
}

.menu > li > .menu__btn > .material-icons-outlined{ margin-right : 16px}

.menu__sub {
  height: 0;
  padding : 0 0 0 48px;
  box-sizing: border-box;
  overflow: hidden;
  transition: height 0.3s ease-in-out;
}

.menu__sub.open{
  height: 130px;
}

.menu__sub > li{
  font-size: 16px;
  margin: 4px 0;
}

.menu__sub > li > a:hover,
.menu__sub > li > a.on{
  color: #ff7a00;
}