/* ****************** 초기화 css ***************** */

* {
  margin: 0;
  padding: 0;
}

body,
button {
  font-weight: normal;
  font-size: 16px;
  font-family: "Roboto", "맑은 고딕", 'Open Sans', sans-serif;
  letter-spacing: -0.3px;
}

html,
body {
  overflow-y: auto;
  overflow-x: hidden;
  min-width: 320px;
  min-height: 320px;
  color: #181818;
}

button {
  cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

ul,
ol,
dl,
li {
  list-style: none;
}

a {
  text-decoration: none;
  color: #181818;
}

address {
  font-style: normal;
}

/* *********************************************** */

/***/
/* 
  color preset
  theme : #ff7a00;

*/

/***/

.font__black { color : #181818 !important;}
.font__white { color : #fff !important;}
.font__theme { color : #ff7a00 !important;}

.text__left {text-align: left;}
.text__left span {margin: auto; justify-content: center;}
.text__center {text-align: center;}
.text__center span {margin: auto; justify-content: center;}
.text__right {text-align: right;}
.text__right span {margin: auto; justify-content: center;}
/***/
.App {
  display: flex;

}
.lnb{
  width: 322px;
  min-width: 322px;
  height: 100vh;
  padding: 16px 16px;
  border-right: 2px solid #ff7a00;
  box-shadow: 1px 0 3px #aaaaaa8e;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between
}

.contents{
  max-height: 100vh;
  padding: 8px 12px;
  box-sizing: border-box;
}

.footer{
  font-size: 12px;
  color: #999;

}
/***/

.menu__tree{
  height: 28px;
  display: flex;
  align-items: center;
  line-height: 32px;
  color: #999;
  margin: 0 0 24px;
}

/***/

.wrap{
  min-width: 680px;
  padding: 0 0 0 24px;
}

.wrap.overflow{
  max-height: 93vh;
}

.wrap.overflow::-webkit-scrollbar {
  width: 8px;
}

.wrap.overflow::-webkit-scrollbar-track {
  background-color: #f1f1f1;
}

.wrap.overflow::-webkit-scrollbar-thumb {
  background-color: #888;
  border-radius: 4px;
}

.wrap.overflow::-webkit-scrollbar-thumb:hover {
  background-color: #555;
}

.wrap__title{
  font-size: 24px;
  color: #ff7a00;
  margin: 0 0 28px;
}



/***/
.input{
  height: 26px;
  margin-bottom: 16px;
  display: flex;
  align-items: flex-end;
}

.input__text,
.input__select{
  width: 280px;
  height: 26px;
  border: none;
  border-bottom: 1px solid #ff7a00;
  outline: 1px solid transparent;
  background-color: transparent;
  padding: 0 11px;
}

.input__text:focus{
  background-color: #ffe0c057;
  border-top: 1px solid transparent;
  border-bottom: 2px solid #ff7a00;
}

.input__text:read-only{
  background-color: transparent;
  border-bottom: 1px dashed #999;
}
.input__text:disabled{
  background-color: #99999928;
  border-bottom: 1px dashed #999;
}

.input__select{
  width: 302px;
  border: 1px solid #ff7a00;
}

.input__checkbox{
  width: 22px;
  height: 22px;
  cursor: pointer
}
.input__radio{
  width: 18px;
  height: 18px;
  cursor: pointer;
}
.label{
  width: 138px;
  margin-right: 16px;
  font-size: 14px;
  display: inline-block;
}


/***/
.button {
  display: inline-block;
  padding: 4px 12px 6px;
  background-color: #ff7a00;
  border: 1px solid transparent;
  color: #fff;
  font-weight: bold;
}
.button:hover{
  box-shadow: 0px 0px 2px #999999;
}
.button:active{
  background-color: #ff7b00ec;
  box-shadow: none;
}

.button.wh {
  display: inline-block;
  padding: 4px 12px 6px;
  background-color: #fff;
  border: 1px solid #ff7a00;
  color: #ff7a00;
  font-weight: bold;
}
.button.wh:hover{
  box-shadow: 0px 0px 2px #999999;
}
.button.wh:active{
  background-color: #fff6eeec;
  box-shadow: none;
}

.button.red {
  display: inline-block;
  padding: 4px 12px 6px;
  background-color: #fff;
  border: 1px solid #ff5959;
  color: #ff5959;
  font-weight: bold;
}
.button.red:hover{
  box-shadow: 0px 0px 2px #999999;
}
.button.red:active{
  background-color: #ffe7e7cb;
  box-shadow: none;
}

.annotation {
  width: auto;
  margin-left: 24px;
  font-size: 14px;
  color: #999;
}

/***/
.divider{
  width: 100%;
  margin: 24px 0;
  border: none;
  border-bottom: 1px solid #999;
}

.divider.sm{
  margin: 8px 0;
}

.divider.dotted{
  border-bottom: 1px dotted #999;
}

.divider.void{
  border: none;
}


/***/

.table{
  width: 100%;
  text-align: left;
  border-collapse: collapse;
}
.table td span,
.table th span{
  display: flex;
  align-items: center;
}
.table thead{
  height: 36px;
  border-bottom: 2px solid #ff7a00;
}

.table thead th{
  height: 36px;
  padding: 0 0 0 32px;
  color: #999;
}

.table tbody{
  border-bottom: 2px solid #ff7a00;
}

.table tbody tr{
  height: 44px;
  border-bottom: 1px solid #999;
}

.table tbody tr:last-child{
  border-bottom: none;
}

.table tbody tr td {
  padding: 0 0 0 32px;
}

.pagination{
  width: 100%;
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.pagination *{
  cursor: pointer;
}

.pagination__chevron.disabled{
  color: #ddd;
  cursor: default
}
.pagination__btn{
  display: block;
  width: 24px;
  line-height: 24px;
  text-align: center;
}

.pagination__btn.now{
  color: #ff7a00;
  font-weight: bold;
}